exto-botao {\r\n  margin: auto;\r\n  font-size: 1.2rem;\r\n}\r\n\r\n.conteudo {\r\n  box-shadow: 0 4px 6px -1px rgb(0 0 0 / 0.1), 0 2px 4px -2px rgb(0 0 0 / 0.1);\r\n  border: .5px solid rgba(0, 0, 0, 0.229);\r\n  border-radius: 5px;\r\n  width: 60vw;\r\n  margin: 5vh auto;\r\n}\r\n\r\n.centralizar-tela {\r\n  position: absolute;\r\n  top: 50%;\r\n  left: 50%;\r\n  transform: translate(-50%, -50%);\r\n}\r\n\r\n.form,\r\n#form {\r\n  box-shadow: 0 4px 6px -1px rgb(0 0 0 / 0.1), 0 2px 4px -2px rgb(0 0 0 / 0.1);\r\n  border: .5px solid rgba(0, 0, 0, 0.229);\r\n  border-radius: 5px;\r\n  width: 50vw;\r\n  margin: 5vh auto;\r\n  padding: 20px;\r\n}\r\n\r\n.topo-conteudo {\r\n  display: grid;\r\n  grid-template-columns: 1fr 2fr 1fr;\r\n  grid-template-areas: \"nome assunto tempo\";\r\n  min-height: 45px;\r\n}\r\n\r\n.nome-grupo {\r\n  grid-area: nome;\r\n  margin: auto 0;\r\n}\r\n\r\n.link-grupo {\r\n  padding: 5px;\r\n  margin-left: 5px;\r\n  transition: box-shadow 0.3s;\r\n}\r\n\r\n.assunto {\r\n  grid-area: assunto;\r\n  margin: auto;\r\n  text-align: center;\r\n  font-weight: 500;\r\n}\r\n\r\n.tempoPostagem {\r\n  text-align: right;\r\n  margin-right: 5px;\r\n}\r\n\r\n.conteudo-slider {\r\n  position: relative;\r\n  overflow: hidden;\r\n}\r\n\r\n.conteudo-texto {\r\n  grid-area: conteudo-texto;\r\n}\r\n\r\n.texto-conteudo {\r\n  padding: 15px;\r\n  margin: 0 auto;\r\n  font-size: 1.2rem;\r\n  width: 90%;\r\n}\r\n\r\n.botoes-config {\r\n  padding: 20px;\r\n  text-align: center;\r\n  display: flex;\r\n  justify-content: center;\r\n}\r\n\r\n.botoes-config button {\r\n  margin-left: 10px;\r\n}\r\n\r\n.conteudo-comentarios {\r\n  background-color: #F2F2F2 !important;\r\n  border-radius: 10px;\r\n  margin: 5px;\r\n  max-height: 630px;\r\n  overflow-y: auto;\r\n  border: 1px solid #ccc;\r\n  padding: 10px;\r\n}\r\n\r\n.form-comentario {\r\n  display: flex;\r\n  justify-content: space-around;\r\n  align-items: center;\r\n  margin: 5px auto;\r\n  width: 100%;\r\n}\r\n\r\n.selecionar-grupo {\r\n  display: flex;\r\n}\r\n\r\n.input-grupo {\r\n  height: 30px;\r\n  display: flex;\r\n  margin: auto 0;\r\n}\r\n\r\n.mensagem-grupo {\r\n  font-size: 1rem;\r\n  margin: auto 10px;\r\n}\r\n\r\n.cursor-pointer {\r\n  cursor: pointer;\r\n}\r\n\r\n.img {\r\n  height: 500px;\r\n  width: 500px;\r\n}\r\n\r\n.imagem-conteudo {\r\n  aspect-ratio: 16 / 9;\r\n  /*PRECISA DISSO*/\r\n  object-fit: cover;\r\n  object-position: center;\r\n  /*Onde ficará o foco da imagem*/\r\n}\r\n\r\n.conteudo .imagem-conteudo {\r\n  width: 100%;\r\n}\r\n\r\n.imagem {\r\n  width: 100%;\r\n}\r\n\r\n.cadastro-login {\r\n  height: 120px;\r\n  border-radius: 5px;\r\n  width: 100%;\r\n  background-color: #24a61f8e;\r\n}\r\n\r\n.nav-verde {\r\n  background-color: #24a61f8e;\r\n}\r\n\r\n.background-cinza {\r\n  background-color: #b7b7b7;\r\n  padding: 10px;\r\n  border-radius: 5px;\r\n}\r\n\r\n.imagem-conteudo {\r\n  width: 100%;\r\n  height: 400px;\r\n  aspect-ratio: 16 / 9;\r\n  object-fit: cover;\r\n  object-position: center;\r\n  border-radius: 5px;\r\n}\r\n\r\n.footer {\r\n  height: 5v;\r\n  position: absolute;\r\n  bottom: 0px;\r\n}\r\n\r\n.descricao {\r\n  overflow: hidden;\r\n  transition: max-height 0.3s ease;\r\n}\r\n\r\n.recolhido {\r\n  max-height: 100px;\r\n}\r\n\r\n.descricaoPadrao {\r\n  height: auto;\r\n  width: auto;\r\n}\r\n\r\n.ler-mais-btn {\r\n  cursor: pointer;\r\n  color: blue;\r\n}\r\n\r\n#coments {\r\n  position: absolute;\r\n  z-index: 5;\r\n}\r\n\r\n@keyframes animacao-abrir {\r\n\r\n  from {\r\n    height: 0;\r\n  }\r\n\r\n  to {\r\n    height: 80vh;\r\n  }\r\n\r\n}\r\n\r\n@keyframes animacao-fechar {\r\n  from {\r\n    height: 80vh;\r\n  }\r\n\r\n  to {\r\n    height: 0;\r\n  }\r\n}\r\n\r\n.feedback {\r\n  display: none;\r\n  width: 100%;\r\n  height: 80vh;\r\n  position: fixed;\r\n  bottom: 0;\r\n  left: 0;\r\n  padding: 2px 0px;\r\n  border-radius: 5px 5px 0 0;\r\n  background-color: #F2F2F2;\r\n  z-index: 2;\r\n}\r\n\r\n.aberto {\r\n  animation-name: animacao-abrir;\r\n  animation-duration: 0.3s;\r\n  animation-direction: normal;\r\n}\r\n\r\n.fechado {\r\n  animation-name: animacao-fechar;\r\n  animation-duration: 0.3s;\r\n  animation-direction: reverse;\r\n}\r\n\r\n.hidden {\r\n  visibility: hidden;\r\n  opacity: 0;\r\n  transition: visibility 0s 300ms, opacity 300ms;\r\n}\r\n\r\n.btn-fechar {\r\n  display: flex;\r\n  top: 5px;\r\n  right: 5px;\r\n  cursor: pointer;\r\n  padding: 15px;\r\n}\r\n\r\n.transparente {\r\n  visibility: hidden;\r\n  opacity: 0;\r\n}\r\n\r\n.display-flex {\r\n  display: flex;\r\n}\r\n\r\n.justify-content-end {\r\n  justify-content: end;\r\n}\r\n\r\n.align-items-start {\r\n  text-align: start;\r\n}\r\n\r\n.mr-10 {\r\n  margin-right: 10px;\r\n}\r\n\r\n.comentario-padrao {\r\n  display: block;\r\n}\r\n\r\n.botao-curtido {\r\n  background-color: #157347;\r\n  color: #fff;\r\n}\r\n\r\n.introducao-postagens {\r\n  margin: 5vh 0 2vh 0;\r\n  display: flex;\r\n  justify-content: center;\r\n}\r\n\r\n.container-botao-fechar {\r\n  display: flex;\r\n  justify-content: end;\r\n  align-items: start;\r\n}\r\n\r\n.btn-completo {\r\n  display: flex;\r\n  margin: 0 auto;\r\n  width: 100%;\r\n  justify-content: center;\r\n}\r\n\r\n.conteudo-cadastro-menu {\r\n  padding: 10px;\r\n}\r\n\r\na {\r\n  text-decoration: none;\r\n  color: black;\r\n}\r\n\r\n/* COMECA AQUI - A PARTIR DAQUI ESTÁ CERTO */\r\n\r\n.grupo {\r\n  background-color: #e7fff0;\r\n  display: inline;\r\n  color: #1A8245;\r\n  padding: 3px;\r\n  border-radius: 20px;\r\n}\r\n\r\n.interacoes {\r\n  display: flex;\r\n  margin-top: 10px;\r\n}\r\n\r\n.btn-interacao:hover {\r\n  background-color: #157347;\r\n  display: inline;\r\n  color: #ffffff;\r\n  height: 10px;\r\n  border-radius: 5px;\r\n  transition: .3s;\r\n}\r\n\r\n.titulo-postagem {\r\n  margin-top: 50px;\r\n  margin-bottom: 15px;\r\n  font-weight: bold;\r\n}\r\n\r\n.opcoes-filtro {\r\n  display: flex;\r\n  flex-wrap: wrap;\r\n  justify-content: center;\r\n  /* Centraliza os itens horizontal